@keyframes fade-in{
    from{
        opacity:0;
    }
    to{
        opacity:1;
    }
  }

  .loadAnimate{
    display: flex;
    animation: fade-in 1s 1;
    float: left;
    text-align: center;
    margin: 0 auto;
    width: 100%;
  }

  .fadeIn{
    animation: fade-in 1s 1;
  }
